import React from "react";
import { MDBBtn, MDBInput, MDBValidation } from "mdb-react-ui-kit";
import Spinner from "../../utils/Spinner";

function Login({
  handleSubmitLogin,
  userLog,
  handleUserLogChange,
  handleDisableLogin,
  handleRegister,
  loading,
}) {
  return (
    <div className="login-form">
      <h3>LOG IN YOUR CREDENTIALS</h3>
      <MDBValidation onSubmit={handleSubmitLogin}>
        <MDBInput
          value={userLog.email}
          onChange={handleUserLogChange}
          name="email"
          className="mt-3"
          label="Email..."
          id="email"
          type="text"
        />
        <MDBInput
          value={userLog.password}
          onChange={handleUserLogChange}
          name="password"
          className="mt-3 mb-3"
          label="Password..."
          id="password"
          type="password"
        />
        <div className="d-grid gap-2">
          <MDBBtn disabled={handleDisableLogin()} type="submit" color="primary">
            log in
            {loading && <Spinner length={4} grow className="ms-2" size="sm" />}
          </MDBBtn>
        </div>
        <div className="text-helper">
          <p>
            Are you a new user? <span onClick={handleRegister}>register</span>
          </p>
        </div>
      </MDBValidation>
    </div>
  );
}

export default Login;
