import "./App.css";
import { useState, useEffect } from "react";
import Homepage from "./components/Homepage";
import { Switch, Route } from "react-router-dom";
import ProtectedRoute from "./utils/ProtectedRoute";
import PostDataPage from "./components/PostDataPage";
import CreateProduct from "./components/CreateProduct";
import axios from "axios";
import authContext from "./components/authentication/authContext";
import urls from "./utils/Urls";
import CartPage from "./components/CartPage";
import jwtDecode from "jwt-decode";
import { toast } from "react-toastify";
import OrderPage from "./components/OrderPage";
import RedirectRoute from "./utils/RedirectRoute";
import SuccessPage from "./components/SuccessPage";

function App() {
  const [category, setCategory] = useState([]);
  const [state, setState] = useState({
    quantity: 0,
    userCart: [],
    address: {},
    loading: false,
    addressOption: {
      address: "",
      city: "",
      state: "",
      country: "",
      zipCode: "",
      phoneNumber: "",
    },
  });
  const getCategories = async () => {
    try {
      const response = await axios.get(urls.getCategories);
      setCategory(response.data);
    } catch (error) {
      console.log(error.response.data);
    }
  };

  const handleAddressChange = (e) => {
    const { name, value } = e.target;
    const addressOption = { ...state.addressOption };
    addressOption[name] = value;
    setState((prevState) => ({
      ...prevState,
      addressOption,
    }));
  };

  const increaseCart = async (id) => {
    const token = localStorage.getItem("token");
    try {
      setState((prevState) => ({
        ...prevState,
        quantity: state.quantity++,
      }));
      const response = await axios.get(urls.increaseCart + id, {
        headers: { Authorization: `JWT ${token}` },
      });
      getUserCart();
      toast.success(response.data);
    } catch (error) {
      console.log(error.response.data);
    }
  };

  const decreaseCart = async (id) => {
    const token = localStorage.getItem("token");
    try {
      // const userCart = [...state.userCart];
      // const cart = userCart.map((cart) => cart.id === id);
      // if (cart.quantity < 1) {
      //   const updatedUserCart = userCart.filter(
      //     (newCart) => newCart.id !== cart.id
      //   );
      //   setState((prevState) => ({
      //     ...prevState,
      //     userCart: updatedUserCart,
      //   }));
      //   toast.warn("product has been removed from the cart");
      // }
      // setState((prevState) => ({
      //   ...prevState,
      //   quantity: state.quantity--,
      // }));
      const response = await axios.get(urls.decreaseCart + id, {
        headers: { Authorization: `JWT ${token}` },
      });
      getUserCart();
      toast.success(response.data);
    } catch (error) {
      console.log(error.response.data);
    }
  };

  const getUserCart = async () => {
    const token = localStorage.getItem("token");
    try {
      const { data } = await axios.get(urls.cartQuantity, {
        headers: { Authorization: `JWT ${token}` },
      });

      setState((prevState) => ({
        ...prevState,
        quantity: data.quantity,
        userCart: data.userCart,
      }));
    } catch (error) {
      console.log(error.response.data);
    }
  };

  const createOrderItem = async () => {
    const token = localStorage.getItem("token");
    try {
      const response = await axios.post(
        urls.createOrder,
        {},
        {
          headers: { Authorization: `JWT ${token}` },
        }
      );
      setState((prevState) => ({
        ...prevState,
        userCart: response.data,
      }));
    } catch (error) {
      console.log(error.response.data);
    }
  };

  const handleAddressSubmit = async (e) => {
    const token = localStorage.getItem("token");
    e.preventDefault();
    setState((prevState) => ({
      ...prevState,
      loading: true,
    }));
    try {
      const newAddress = { ...state.addressOption };
      await axios.post(urls.createOrderAddress, newAddress, {
        headers: { Authorization: `JWT ${token}` },
      });
      setTimeout(() => {
        setState((prevState) => ({
          ...prevState,
          loading: false,
        }));
        getUserAddress();
      }, 2000);
    } catch (error) {
      console.log(error.response.data);
    }
  };

  const getUserAddress = async () => {
    const token = localStorage.getItem("token");
    try {
      const response = await axios.get(urls.getOrderAddress, {
        headers: { Authorization: `JWT ${token}` },
      });
      setState((prevState) => ({
        ...prevState,
        address: response.data,
      }));
    } catch (error) {
      console.log(error.response.data);
    }
  };

  useEffect(() => {
    const token = localStorage.getItem("token");
    getCategories();
    if (token) {
      getUserCart();
      getUserAddress();
      const user = jwtDecode(token);
      const time = new Date(user.exp * 1000);
      const difference = time - new Date();
      setTimeout(() => {
        alert("your session has expired");
        localStorage.removeItem("token");
        window.location.href = "/login";
      }, difference);
    }
  }, []);

  const { quantity, userCart, addressOption, loading, address } = state;

  return (
    <authContext.Provider
      value={{
        category,
        quantity,
        userCart,
        loading,
        getUserCart,
        increaseCart,
        decreaseCart,
        handleAddressChange,
        addressOption,
        createOrderItem,
        handleAddressSubmit,
        getUserAddress,
        address,
      }}
    >
      <Switch>
        <Route path="/login" component={Homepage} />
        <ProtectedRoute path="/home" component={PostDataPage} />
        <ProtectedRoute path="/order-page" component={OrderPage} />
        <ProtectedRoute path="/cart" component={CartPage} />
        <ProtectedRoute
          path="/create-product"
          render={(props) => <CreateProduct {...props} category={category} />}
        />
        <ProtectedRoute path="/payment/successful" component={SuccessPage} />
        <RedirectRoute />
      </Switch>
    </authContext.Provider>
  );
}

export default App;
