import React from "react";
import { MDBSpinner } from "mdb-react-ui-kit";

function Spinner({ length, className, grow, size }) {
  let finalSpinner = [];
  for (let i = 0; i < length; i++) {
    finalSpinner.push(
      <MDBSpinner
        key={i}
        size={size}
        grow={grow}
        role="status"
        tag="span"
        className={className}
      />
    );
  }
  return finalSpinner;
}

export default Spinner;
