import React, { useState, useEffect } from "react";
import axios from "axios";
import urls from "../utils/Urls";
import { MDBBadge, MDBBtn, MDBIcon } from "mdb-react-ui-kit";
import { Link } from "react-router-dom";
import authContext from "./authentication/authContext";
import Spinner from "../utils/Spinner";
import CartItem from "./CartItem";

function ProductsPage(props) {
  const [state, setState] = useState({
    products: [],
    loading: false,
    filterOption: "",
    selectedCategory: "",
  });
  const { category, getUserCart, quantity } = React.useContext(authContext);
  const handleDeleteProduct = async (slug) => {
    try {
      await axios.get(urls.deleteProduct + slug);
      setTimeout(() => {
        window.location.reload();
      }, 1500);
    } catch (error) {
      console.log(error.response.data);
    }
  };

  useEffect(() => {
    getProducts();
    getUserCart();
  }, []);

  const getProducts = async () => {
    setState({ ...state, loading: true });
    try {
      const {
        data: { products },
      } = await axios.get(urls.getProducts);
      console.log(products);
      setState({ ...state, products, loading: false });
    } catch (error) {
      console.log(error.response.data);
    }
  };

  const getCategoryProducts = async (slug) => {
    setState({ ...state, loading: true });
    try {
      const response = await axios.get(urls.getProductCat + slug);
      setTimeout(() => {
        setState({
          ...state,
          products: response.data?.products,
          loading: false,
        });
      }, 2000);
    } catch (error) {
      console.log(error.response.data);
    }
  };

  const handleFilterChange = (e) => {
    const { value } = e.target;
    setState({ ...state, filterOption: value });
  };

  const handleFilterPrice = async ({ target: { value } }) => {
    setState({ ...state, loading: true });
    const filterValue = {
      filterValue: value,
    };
    try {
      const response = await axios.post(urls.filterProducts, filterValue);
      setTimeout(() => {
        setState({ ...state, loading: false, products: response.data });
      }, 2000);
    } catch (error) {
      console.log(error.response.data);
    }
  };

  const handleCategoryChange = ({ target: { value } }) => {
    const { name } = JSON.parse(value),
      { slug } = JSON.parse(value);
    setState((prevState) => ({
      ...prevState,
      selectedCategory: name,
    }));
    getCategoryProducts(slug);
  };

  const subString = (string) => {
    return string.substring(0, 50);
  };

  const addToCart = async (id) => {
    const alertButton = document.querySelector(".my-center");
    const token = localStorage.getItem("token");
    const product = {
      productId: id,
    };
    try {
      await axios.post(urls.addToCart, product, {
        headers: { Authorization: `JWT ${token}` },
      });
      alertButton.classList.add("open");
      getUserCart();
    } catch (err) {
      console.log(err.response.data);
    }
  };

  const closeButton = () => {
    const alertButton = document.querySelector(".my-center");
    if (alertButton.classList.contains("open")) {
      alertButton.classList.remove("open");
    }
  };

  const returnProducts = (myProducts) => {
    if (myProducts?.length > 0) {
      return (
        <div className="container-fluid">
          <div className="row m-auto">
            {myProducts.map((product) => {
              return (
                <div key={product.id} className="col-12 col-md-3">
                  <div className="card">
                    <img
                      src={urls.getImage + product.image}
                      className="card-img-top"
                      alt="product"
                    />
                    <div className="card-header">
                      <h4 className="text-center">
                        {product.name}{" "}
                        <MDBBadge
                          onClick={() =>
                            getCategoryProducts(product.Category.slug)
                          }
                          style={{ cursor: "pointer" }}
                          className="ms-4"
                          pill
                          color="black"
                        >
                          {product.Category.name}
                        </MDBBadge>
                      </h4>
                    </div>
                    <div className="card-body">
                      <p className="card-text">
                        {subString(product.description) + "..."}
                      </p>
                      <p className="float-end text-black-200 bolder-text">
                        ${product.price}
                      </p>
                      <br />
                      <div className="d-grid gap-2">
                        <button
                          onClick={() => addToCart(product.id)}
                          className="btn btn-warning"
                        >
                          <i
                            style={{ fontSize: "15px", marginRight: "10px" }}
                            className="fas fa-shopping-cart"
                          />
                          add to cart
                        </button>
                        {props.user.admin && (
                          <button
                            type="button"
                            onClick={() => handleDeleteProduct(product.slug)}
                            className="btn btn-danger"
                          >
                            <i
                              style={{ fontSize: "15px", marginRight: "10px" }}
                              className="fas fa-trash-alt"
                            />
                            delete product
                          </button>
                        )}
                      </div>
                    </div>
                  </div>
                </div>
              );
            })}
          </div>
        </div>
      );
    } else
      return (
        <div className="d-flex w-100 my-height justify-content-center align-items-center">
          <div className="text-center d-flex flex-column align-items-center justify-content-center">
            <i
              style={{ fontSize: "60px", fontWeight: "bold" }}
              className="fas fa-code"
            />
            <h5>You have no products currently to show</h5>
          </div>
        </div>
      );
  };

  const returnFilter = (filter) => {
    if (filter === "") return;
    else if (filter === "Category") {
      return (
        <select
          defaultValue={state.selectedCategory}
          onChange={handleCategoryChange}
          className="form-select ms-3 my-width"
        >
          <option value=""></option>;
          {category.map((cat) => {
            return (
              <option key={cat.id} value={JSON.stringify(cat)}>
                {cat.name}
              </option>
            );
          })}
        </select>
      );
    }
    return (
      <select
        onChange={handleFilterPrice}
        className="form-select ms-3 my-width"
      >
        <option value=""></option>
        <option value={1}>less than $50</option>
        <option value={2}>between $50 and $200</option>
        <option value={3}>greater than $200</option>
      </select>
    );
  };

  if (state.loading)
    return (
      <div className="loading">
        <h4>Loading...</h4>
        <div className="spinner-side">
          <Spinner className="me-3" length={4} size="lg" grow tag="span" />
        </div>
      </div>
    );

  return (
    <React.Fragment>
      <div className="my-center">
        <CartItem closeButton={closeButton} />
      </div>
      <div className="d-flex justify-content-center align-items-center mt-3">
        <MDBBtn onClick={getProducts} size="sm" type="button" color="primary">
          All products
        </MDBBtn>
        <select
          onChange={handleFilterChange}
          defaultValue={state.filterOption}
          className="form-select my-width ms-3"
        >
          <option value="">Filter products by</option>
          <option value="Category">Category</option>
          <option value="Price">Price</option>
        </select>
        {returnFilter(state.filterOption)}
        <div className="cart-section flex-grow-0 ms-4">
          <Link to="/cart">
            <MDBIcon fas icon="shopping-cart" size="2x" />
            <MDBBadge className="fs-5" color="danger" notification pill>
              {quantity}
            </MDBBadge>
          </Link>
        </div>
      </div>
      <hr />
      <div className="mt-5">{returnProducts(state.products)}</div>
    </React.Fragment>
  );
}

export default ProductsPage;
