import React from "react";
import authContext from "./authentication/authContext";
import { withRouter } from "react-router-dom";

function CartPage({ history }) {
  const { userCart, increaseCart, decreaseCart } =
    React.useContext(authContext);
  const grossTotal = () => {
    let total = 0;
    userCart.map((cart) => {
      total += cart.product?.price * cart.quantity;
    });
    return `$${total}`;
  };
  return (
    <React.Fragment>
      {userCart.length > 0 ? (
        <>
          <div className="main-cart-page">
            <h4 className="text-center bolder">Your Shopping Cart</h4>
            <div className="product-details">
              <div className="product-page">
                <h4>Product Name</h4>
                {userCart.map((cart) => {
                  return (
                    <React.Fragment key={cart.id}>
                      <h5>{cart.product?.name}</h5>
                    </React.Fragment>
                  );
                })}
              </div>
              <div className="product-quantity">
                <h4>product quantity</h4>
                {userCart.map((cart) => {
                  return (
                    <React.Fragment key={cart.id}>
                      <div className="quantity-product">
                        <div className="my-button">
                          <button
                            onClick={() => decreaseCart(cart.id)}
                            className="first-button"
                          >
                            <i className="fas fa-minus" />
                          </button>
                        </div>
                        <h5>{cart.quantity}</h5>
                        <div
                          onClick={() => increaseCart(cart.id)}
                          className="my-button"
                        >
                          <button className="first-button">
                            <i className="fas fa-plus" />
                          </button>
                        </div>
                      </div>
                    </React.Fragment>
                  );
                })}
              </div>
              <div className="product-price">
                <h4>product price</h4>
                {userCart.map((cart) => {
                  return (
                    <React.Fragment key={cart.id}>
                      <h5>${cart.product?.price}</h5>
                    </React.Fragment>
                  );
                })}
              </div>
              <div className="sub-total">
                <h4>Sub Total</h4>
                {userCart.map((cart) => {
                  return (
                    <React.Fragment key={cart.id}>
                      <h4 className="text-black">
                        ${cart.product?.price * cart.quantity}
                      </h4>
                    </React.Fragment>
                  );
                })}
                <div className="total-page">
                  <h4>{grossTotal()}</h4>
                </div>
              </div>
            </div>
          </div>
          <div className="order-detail">
            <button
              onClick={() => history.push("/home")}
              className="btn btn-warning"
            >
              Continue shopping
            </button>
            <button
              onClick={() => history.push("/order-page")}
              className="btn btn-success"
            >
              Checkout
            </button>
          </div>{" "}
        </>
      ) : (
        <>
          <div className="d-flex vh-100 flex-column justify-content-center align-items-center mx-auto text-center">
            <i
              style={{ fontSize: "30px", marginBottom: "20px" }}
              className="fas fa-shopping-cart"
            />
            <h4 className="mb-4 ">Your shopping cart is empty</h4>
            <button
              onClick={() => history.push("/home")}
              className="btn btn-warning"
            >
              continue shopping
            </button>
          </div>
        </>
      )}
    </React.Fragment>
  );
}

export default withRouter(CartPage);
