import React, { useState } from "react";
import axios from "axios";
import urls from "../utils/Urls";
import Login from "./authentication/Login";
import Register from "./authentication/Register";
import { toast } from "react-toastify";

const Homepage = ({ history }) => {
  const [state, setState] = useState({
    userReg: {
      email: "",
      password1: "",
      password2: "",
      firstName: "",
      lastName: "",
    },
    userLog: { email: "", password: "" },
    loading: false,
    errors: {},
    gender: "",
  });
  const handleLogin = () => {
    const registerForm = document.querySelector(".registration-section");
    const loginForm = document.querySelector(".login-section");
    if (registerForm.classList.contains("show")) {
      registerForm.classList.remove("show");
    }
    loginForm.classList.remove("close");
  };

  const handleRegister = () => {
    const registerForm = document.querySelector(".registration-section");
    const loginForm = document.querySelector(".login-section");
    registerForm.classList.add("show");
    loginForm.classList.add("close");
  };

  const handleUserLogChange = (e) => {
    const { name, value } = e.target;
    const userLog = { ...state.userLog };
    userLog[name] = value;
    setState((prevState) => ({
      ...prevState,
      userLog,
    }));
  };

  const handleRegisterChange = (e) => {
    const { name, value } = e.target;
    const userReg = { ...state.userReg };
    userReg[name] = value;
    setState((prevState) => ({
      ...prevState,
      userReg,
    }));
  };

  const handleDisableRegister = () => {
    const { firstName, lastName, email, password1, password2 } = state.userReg;
    return (
      firstName === "" ||
      lastName === "" ||
      email === "" ||
      password1 === "" ||
      password2 === "" ||
      state.gender === "" ||
      state.loading
    );
  };

  const handleDisableLogin = () => {
    return (
      state.userLog.email === "" ||
      state.userLog.password === "" ||
      state.loading
    );
  };

  const handleGender = (e) => {
    setState({ ...state, gender: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setState({ ...state, loading: true });
    const { email, firstName, lastName, password1, password2 } = state.userReg;
    const userOptions = {
      email: email.toLowerCase(),
      firstName,
      lastName,
      password: password1,
      password2,
      gender: state.gender,
    };
    try {
      const response = await axios.post(urls.createUser, userOptions);
      await localStorage.setItem("token", response.data["token"]);
      setTimeout(() => {
        setState({ ...state, loading: false });
        history.push("/home", {
          message: "Your registration has been succesful",
        });
      }, 3000);
    } catch (error) {
      const errors = { ...state.errors };
      errors["email"] = error.response.data["message"];
      setState((prevState) => ({
        ...prevState,
        userReg: { password1: "", password2: "" },
        errors,
        loading: false,
      }));
      console.log(error.response.data);
    }
  };

  const handleSubmitLogin = async (e) => {
    e.preventDefault();
    setState({ ...state, loading: true });
    const { email, password } = state.userLog;
    const userOptions = {
      email: email.toLowerCase(),
      password,
    };
    try {
      const response = await axios.post(urls.loginUser, userOptions);
      localStorage.setItem("token", response.data.user["token"]);
      setTimeout(() => {
        setState({ ...state, loading: false });
        window.location.href = "/home";
      }, 3000);
    } catch (error) {
      setState({ ...state, loading: false });
      toast.error(error.response.data["message"]);
      console.log(error.response.data);
    }
  };

  const { userReg, userLog, errors, loading } = state;
  return (
    <React.Fragment>
      <div className="main-page">
        <div className="divided-page">
          <div className="background-page">
            <img src="static/images/account.svg" alt="my svg" />
          </div>

          <div className="account-page">
            <h5>My Account Data Page</h5>
            <div className="text-account">
              <div className="login-section">
                <Login
                  handleSubmitLogin={handleSubmitLogin}
                  userLog={userLog}
                  handleUserLogChange={handleUserLogChange}
                  handleDisableLogin={handleDisableLogin}
                  handleRegister={handleRegister}
                  loading={loading}
                />
              </div>
              <div className="registration-section">
                <Register
                  handleSubmit={handleSubmit}
                  errors={errors}
                  handleRegisterChange={handleRegisterChange}
                  handleLogin={handleLogin}
                  handleGender={handleGender}
                  userReg={userReg}
                  handleDisableRegister={handleDisableRegister}
                  loading={loading}
                />
              </div>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
};
export default Homepage;
