import React, { useEffect, useState } from "react";
import { MDBInput, MDBBtn } from "mdb-react-ui-kit";
import authContext from "./authentication/authContext";
import Spinner from "../utils/Spinner";
import axios from "axios";
import urls from "../utils/Urls";
import { loadStripe } from "@stripe/stripe-js";
import { Elements } from "@stripe/react-stripe-js";
import CheckoutForm from "./CheckoutForm";

const stripePromise = loadStripe(
  "pk_test_51JnsD9LTs9CoXxdPAJujbIZugH424v04e1Q1brLNavLcoaLX1Gck02VZhKTnsluNg9XiuBGCOLwIuYxe1cAUznSs00WXwsu3pb"
);

function OrderPage({ history }) {
  const [clientSecret, setClientSecret] = useState("");
  const {
    handleAddressChange,
    addressOption,
    createOrderItem,
    handleAddressSubmit,
    address,
    userCart,
    loading,
    getUserAddress,
  } = React.useContext(authContext);

  useEffect(() => {
    createOrderItem();
    getUserAddress();
    createPayment();
  }, []);

  const createPayment = async () => {
    const cart = {
      total: userCart,
    };
    try {
      const response = await axios.post(urls.orderPayment, cart);
      console.log(response);
      setClientSecret(response.data.clientSecret);
    } catch (error) {
      console.log(error.response.data);
    }
  };
  const appearance = {
    theme: "night",
  };
  const options = {
    clientSecret,
    appearance,
  };

  const shipping = 10;

  const getTotal = () => {
    let total = 0;
    userCart.map((cart) => {
      return (total += cart.product?.price * cart.quantity);
    });
    return total + shipping;
  };

  return (
    <React.Fragment>
      <div className="order-page">
        {!address || address === null ? (
          <div className="address-page">
            <h4>Billing and Shipping Address</h4>
            <div className="form-page">
              <form onSubmit={handleAddressSubmit}>
                <MDBInput
                  onChange={handleAddressChange}
                  name="address"
                  value={addressOption.address}
                  className="mb-3"
                  label="address"
                  type="text"
                  required
                />
                <MDBInput
                  onChange={handleAddressChange}
                  name="city"
                  value={addressOption.city}
                  className="mb-3"
                  label="city"
                  type="text"
                  required
                />
                <MDBInput
                  onChange={handleAddressChange}
                  name="state"
                  value={addressOption.state}
                  className="mb-3"
                  label="state"
                  type="text"
                  required
                />
                <MDBInput
                  onChange={handleAddressChange}
                  name="country"
                  value={addressOption.country}
                  className="mb-3"
                  label="country"
                  type="text"
                  required
                />
                <MDBInput
                  onChange={handleAddressChange}
                  name="zipCode"
                  value={addressOption.zipcode}
                  className="mb-3"
                  label="zipcode"
                  type="text"
                  required
                />
                <MDBInput
                  onChange={handleAddressChange}
                  name="phoneNumber"
                  value={addressOption.phoneNumber}
                  className="mb-3"
                  label="phone number"
                  type="text"
                  required
                />
                <div className="d-grid">
                  <MDBBtn color="success" size="lg">
                    add address
                    {loading && (
                      <Spinner
                        className="ms-4"
                        length={4}
                        size="sm"
                        tag="span"
                        grow
                      />
                    )}
                  </MDBBtn>
                </div>
              </form>
            </div>
          </div>
        ) : (
          <>
            <div className="card w-75 ">
              <div className="card-header bg-success text-white">
                <h5 className="text-center">Your address detail</h5>
              </div>
              <div className="card-body">
                <div className="d-flex flex-column justify-content-center align-items-center">
                  <h5 className="mt-4">
                    Full Name :{" "}
                    <strong>
                      {address.user?.firstName + " " + address.user?.lastName}
                    </strong>
                  </h5>
                  <h5 className="mt-4">
                    Address : <strong>{address.address}</strong>
                  </h5>
                  <h5 className="mt-4">
                    City : <strong>{address.city}</strong>
                  </h5>
                  <h5 className="mt-4">
                    State : <strong>{address.state}</strong>
                  </h5>
                  <h5 className="mt-4">
                    Zip-code : <strong>{address.zipCode}</strong>
                  </h5>
                  <h5 className="mt-4">
                    Phone number : <strong>{address.phoneNumber}</strong>
                  </h5>
                </div>
              </div>
              <div className="d-grid gap-2 mt-4">
                <button
                  onClick={() => history.push("/home")}
                  className="btn btn-primary"
                >
                  <i
                    style={{
                      fontSize: "15px",
                      marginRight: "18px",
                    }}
                    className="fas fa-arrow-left"
                  />
                  continue shopping
                </button>
              </div>
            </div>
            <div className="border-with"></div>
          </>
        )}
        <div className="order-price-page">
          <h4 className="mb-4">Order Detail</h4>
          <div className="card p-2">
            <div className="d-flex w-100 flex-column">
              {userCart.map((cart) => {
                return (
                  <React.Fragment key={cart.id}>
                    <div className="d-flex">
                      <div className="me-auto flex-grow-3">
                        <h5 className="ms-2">
                          {cart.product?.name}
                          {"   "}
                          <span className="ms-3">x{cart.quantity}</span>
                        </h5>
                      </div>
                      <div className="">
                        <h5 className="">
                          ${cart.product?.price * cart.quantity}
                        </h5>
                      </div>
                    </div>
                    <hr />
                  </React.Fragment>
                );
              })}
              <div className="d-flex">
                <div className="me-auto">
                  <h3 className="px-2">Shipping</h3>
                </div>
                <div className="p-1">
                  <h3>{shipping}</h3>
                </div>
              </div>
              <hr />
              <div className="d-flex">
                <div className="me-auto">
                  <h3 className="px-2">Total</h3>
                </div>
                <div className="p-1">
                  <h3>${getTotal()}</h3>
                </div>
              </div>
            </div>
          </div>
          <div className="d-grid mt-3">
            <button className="btn btn-warning">
              <i
                style={{ marginRight: "12px" }}
                className="fas fa-credit-card"
              />
              pay your order
            </button>
          </div>
          <div className="">
            {clientSecret && (
              <Elements options={options} stripe={stripePromise}>
                <CheckoutForm />
              </Elements>
            )}
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}

export default OrderPage;
