import React, { useState } from "react";
import { withRouter } from "react-router-dom";
import { MDBInput, MDBBtn } from "mdb-react-ui-kit";
import Spinner from "../utils/Spinner";
import axios from "axios";
import urls from "../utils/Urls";

function CreateProduct({ history, category }) {
  const [state, setState] = useState({
    product: { name: "", description: "", stock: "", price: "" },
    loading: false,
    errors: {},
    productsValue: [""],
    categoryId: "",
  });
  const [image, setImage] = useState("");

  const handleProductChange = (e) => {
    const { name, value } = e.target;
    const product = { ...state.product };
    product[name] = value;
    setState({ ...state, product });
  };

  const handleImageChange = (e) => {
    if (e.target.files[0] !== undefined || e.target.files[0] === null) {
      setImage(e.target.files[0]);
      const type = e.target.files[0].type;
      const imageExtension = ["image/jpeg", "image/png"];
      const findFile = imageExtension.indexOf(type);
      const errors = { ...state.errors };
      if (findFile === -1) errors["image"] = "Only image extension is allowed";
      else delete errors["image"];
      setState({ ...state, errors });
    }
  };

  // const handleAddValue = () => {
  //   const productsValue = [...state.productsValue, ""];
  //   setState({ ...state, productsValue });
  // };

  // const handleValueChange = (e, index) => {
  //   const productsValue = [...state.productsValue];
  //   productsValue[index] = e.target.value;
  //   setState({ ...state, productsValue });
  //   console.log(state.productsValue);
  // };

  // const handleRemoveValue = (index) => {
  //   const productsValue = [...state.productsValue];
  //   productsValue.splice(index, 1);
  //   setState({ ...state, productsValue });
  // };

  const handleCategoryChange = (e) => {
    setState({ ...state, categoryId: e.target.value });
  };

  const handleProductSubmit = async (e) => {
    e.preventDefault();
    setState({ ...state, loading: true });
    const { product } = state;
    if (state.categoryId === "") {
      alert("category must be selected");
      setState({ ...state, loading: false });
      return;
    }
    const formArray = new FormData();
    formArray.append("name", product.name);
    formArray.append("stock", Number(product.stock));
    formArray.append("price", Number(product.price));
    formArray.append("description", product.description);
    formArray.append("CategoryId", state.categoryId);
    formArray.append("image", image, image.name);
    try {
      const response = await axios.post(urls.createProduct, formArray, {
        headers: { Content_Type: "multipart/form-data" },
      });
      console.log(response);
      setTimeout(() => {
        setState({ ...state, loading: false });
        history.push("/home", {
          message: response.data["message"],
        });
      }, 3000);
    } catch (error) {
      console.log(error.response.data);
    }
  };

  const { product, loading, errors } = state;
  return (
    <React.Fragment>
      <div className="container w-100 mt-5 text-center d-flex justify-content-center align-items-center">
        <div className="col-12 col-md-6">
          <h4>Create your product</h4>
          <div className="form-input">
            <form onSubmit={handleProductSubmit} className="form-group">
              <MDBInput
                onChange={handleProductChange}
                name="name"
                value={product.name}
                type="text"
                label="Product Name"
                required
                className="mt-3"
              />
              <MDBInput
                onChange={handleProductChange}
                name="price"
                type="number"
                required
                value={product.price}
                label="Product Price"
                className="mt-3"
              />
              <MDBInput
                onChange={handleProductChange}
                name="stock"
                type="number"
                required
                value={product.stock}
                label="Product stock"
                className="mt-3"
              />
              <MDBInput
                type="text"
                label="Product description"
                textarea
                value={product.description}
                name="description"
                onChange={handleProductChange}
                rows={4}
                required
                className="mt-3"
              />
              <select
                onChange={handleCategoryChange}
                className="form-select mt-3"
                defaultValue={state.categoryId}
              >
                <option value="">select category</option>
                {category.map((cat) => {
                  return (
                    <option value={cat.id} key={cat.slug}>
                      {cat.name}
                    </option>
                  );
                })}
              </select>
              <label className="form-label mt-3" htmlFor="customeFile">
                Product Image
              </label>
              <input
                required
                className="form-control mt-2"
                type="file"
                onChange={handleImageChange}
                id="customFile"
              />
              {errors?.image && (
                <span style={{ color: "red" }}>{errors.image}</span>
              )}
              <div className="d-grid gap-2 mt-3">
                <MDBBtn
                  disabled={loading}
                  type="submit"
                  size="lg"
                  color="success"
                >
                  Create product
                  {loading && (
                    <Spinner
                      className="ms-4"
                      length={4}
                      grow
                      size="sm"
                      tag="span"
                    />
                  )}
                </MDBBtn>
              </div>
              <div
                onClick={() => history.replace("/home")}
                className="d-grid gap-2 mt-3"
              >
                <MDBBtn type="button" size="lg" color="black">
                  <i
                    style={{ marginRight: "20px" }}
                    className="fas fa-arrow-left"
                  />
                  Go back
                </MDBBtn>
              </div>
            </form>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}

export default withRouter(CreateProduct);
