import React from "react";
import { withRouter } from "react-router-dom";

function CartItem({ closeButton, history }) {
  return (
    <React.Fragment>
      <div className="container-fluid">
        <div className="d-flex  h-100 justify-content-center align-items-center">
          <div className="card w-45 h-45">
            <div className="card-body">
              <div
                onClick={closeButton}
                className="position-absolute top-0 end-0 me-3 p-1 pointer"
              >
                <i className="fas fa-times" />
              </div>

              <p className="text-center text-black-50 py-4">
                <strong>Item has been added to cart</strong>
              </p>
              <div className="d-grid ">
                <button
                  onClick={() => history.push("/cart")}
                  className="btn btn-success"
                >
                  view cart
                </button>
                <br />
                <button className="btn btn-warning">continue shopping</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </React.Fragment>
  );
}

export default withRouter(CartItem);
