import React from "react";
import { MDBBtn, MDBInput, MDBRadio, MDBValidation } from "mdb-react-ui-kit";
import Spinner from "../../utils/Spinner";

function Register({
  handleSubmit,
  errors,
  userReg,
  handleRegisterChange,
  handleLogin,
  handleGender,
  handleDisableRegister,
  loading,
}) {
  return (
    <div className="registration-form">
      <h3>REGISTER YOUR CREDENTIALS</h3>
      <MDBValidation onSubmit={handleSubmit}>
        <MDBInput
          className={errors.email ? "is-invalid mt-3" : "mt-3"}
          value={userReg.email}
          onChange={handleRegisterChange}
          name="email"
          id="validationCustom01"
          label="Email..."
          // id="email"
          required
          type="text"
        />
        {errors.email && (
          <span style={{ color: "red", marginBottom: "5px" }}>
            {errors.email}
          </span>
        )}
        <MDBInput
          value={userReg.password1}
          onChange={handleRegisterChange}
          className="mt-3 mb-3"
          name="password1"
          label="Password..."
          id="password"
          type="password"
        />
        <MDBInput
          value={userReg.password2}
          onChange={handleRegisterChange}
          name="password2"
          className="mt-3 mb-3"
          label="confirm password"
          id="password2"
          type="password"
        />
        <MDBInput
          value={userReg.firstName}
          onChange={handleRegisterChange}
          name="firstName"
          className="mt-3 mb-3"
          label="First Name"
          id="firstName"
          type="text"
        />
        <MDBInput
          value={userReg.lastName}
          onChange={handleRegisterChange}
          name="lastName"
          className="mt-3 mb-3"
          label="Last Name"
          id="lastName"
          type="text"
        />
        <div className="mt-3 d-flex justify-content-center align-items-center">
          <MDBRadio
            name="inlineRadio"
            id="inlineRadio1"
            value="Male"
            label="Male"
            onChange={handleGender}
            inline
          />
          <MDBRadio
            name="inlineRadio"
            id="inlineRadio2"
            value="Female"
            label="Female"
            onChange={handleGender}
            inline
          />
        </div>
        <div className="d-grid gap-2">
          <MDBBtn
            disabled={handleDisableRegister()}
            type="submit"
            color="success"
          >
            Register
            {loading && <Spinner className="ms-4" length={4} size="sm" grow />}
          </MDBBtn>
        </div>
        <div className="text-helper">
          <p>
            Are you registered? <span onClick={handleLogin}>login</span>
          </p>
        </div>
      </MDBValidation>
    </div>
  );
}

export default Register;
